.salesFrame{
  font-family: sans-serif;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(rgba(0, 0, 0, 0.65), rgba(0, 0, 0, 0.65)), url('../../../img/ProductsScreen/photos/background/agroforest.jpg') center center;
  background-attachment: fixed;
  background-size: cover;
  background-blend-mode: darken;
  overflow: auto;

  .profileImg {
    width: 100%;
    border-style: hidden;
    border-color: black;
    border-width: 4px;
    margin: auto;
    box-shadow: 0px 0px 7px Black, 0px 0px 7px Black;

  }

  h3.profileName{
    color: azure;
    font-weight: bold;
    font-size: 20px;
    text-align: center;
  }

  h3.productType {
    display: block;
    position: relative;
    text-align: center;
    font-size:  1.5rem;
    font-weight: bold;
    clear: both;
    color: azure;
    padding: 0;
    padding-top: 5vh;
    margin: 0;
  }

  .productsDiv{
    display:flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
    margin: 0;
    padding: 2vw;
  }
}